import React from "react"
import Layout from "../components/layout";
import { StaticQuery, graphql } from 'gatsby'
import styled from 'styled-components'
import Img from 'gatsby-image'
import { withPrefix } from 'gatsby'

const Resume = () => (
    <StaticQuery
      query={graphql`
        query {
          placeholderImage: file(relativePath: { eq: "Resume.png" }) {
            childImageSharp {
              fluid(maxWidth: 1024) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      `}
      render={data => <Img fluid={data.placeholderImage.childImageSharp.fluid} />}
    />
  )

  const ResumeWrapper = styled.div`
  width: 90%;
  text-align: center;
  margin: 0px auto;
  textDecoration: none;
  box-shadow: 8px 8px 8px 8px rgba(0,0,0,0.18);
`

export default () => (

    <Layout>
        <h1 align="center">ABOUT</h1>
        <br />
        <h3>A little bit about me</h3>
        <blockquote>
            <p> I am glad you made it to my website to learn a little bit more about me. I am a software developer currently working at
                General Motors in Austin Texas. I also do part time development for small businesses. <a href="https://github.com/saffan786?tab=repositories">Check out some of the things I have worked on.</a>
                </p>
        </blockquote>
        <br />
        <h3>What else is there to know about me?</h3>
        <blockquote>
            <ol>
                <li>I am a huge sports fan and enjoy both watching and playing different sports.</li>
                <li>I believe in exercise and a healthy way of life.</li>
                <li>I am always wanting to learn new things, so throw a problem my way.</li>
            </ol>
        </blockquote>
        <br />
        <h3>Here is my resume if you want more information about me.</h3>
        <a href={withPrefix("static/resume/Business_Resume.pdf")}>
        <ResumeWrapper>
           <Resume/>
        </ResumeWrapper>
        </a>
     
    </Layout>

)